<template>
  <div :class="`${prefixCls}-status`">
    <section v-if="audit.comment != null">
      <section
        v-if="audit.expires_at != null"
        class="gray">
        <span class="amount-show">{{ audit.amount }} / {{ audit.day }}</span>
      </section>
      <section
        v-else
        class="green"
        @click="showOperations(audit)">
        <span class="audit-show">{{ audit.amount }} / {{ audit.day }}</span>
        <span class="audit-operation"></span>
      </section>
    </section>
    <section
      v-if="audit.comment == null"
      class="red">
      
    </section>
  </div>
</template>

<script>
/**
 * 
 */

const prefixCls = "msgList";
export default {
  name: "FeedCommentAuditStatus",
  props: {
    audit: { type: Object, default: () => {} }
  },
  data: () => ({
    prefixCls
  }),
  methods: {
    /**
     * 
     * @Author   Wayne
     * @DateTime 2018-02-08
     * @Email    qiaobin@zhiyicx.com
     * @return   {[type]}            [description]
     */
    accept() {
      const { currentItem = {} } = this;
      const {
        raw: feedId = 0,
        target: commentId = 0,
        id: pinnedId = 0
      } = currentItem;
      this.$Modal.remove();
      this.$http
        .patch(
          `/feeds/${feedId}/comments/${commentId}/currency-pinneds/${pinnedId}`,
          {
            validateStatus: s => s === 201
          }
        )
        .then(({ data }) => {
          this.audit.expires_at = 1;
          this.$Message.success(data);
        })
        .catch(() => {});
    },
    /**
     * 
     * @Author   Wayne
     * @DateTime 2018-02-08
     * @Email    qiaobin@zhiyicx.com
     * @return   {[type]}            [description]
     */
    reject() {
      const { currentItem = {} } = this;
      const { id: pinnedId = 0 } = currentItem;
      this.$Modal.remove();
      this.$http
        .delete(`/user/feed-comment-currency-pinneds/${pinnedId}`, {
          validateStatus: s => s === 204
        })
        .then(() => {
          this.audit.expires_at = 1;
          this.$Message.success("");
        })
        .catch(() => {});
    },
    showOperations(audit) {
      if (audit.feed === null) {
        this.$Message.error(",");
        return false;
      }
      this.currentItem = audit;
      const vm = this;
      this.$Modal.info({
        title: false,
        render(h) {
          return h("div", {}, [
            h(
              "button",
              {
                on: {
                  click: vm.accept
                }
              },
              ""
            ),
            h(
              "button",
              {
                on: {
                  click: vm.reject
                }
              },
              ""
            )
          ]);
        }
      });
    }
  }
};
</script>
<style lang="less" src="../style.less">
</style>
