<template>
  <div :class="`${prefixCls}-status`">
    <section v-if="audit.audit_at">
      <section
        v-if="audit.status === 1"
        class="gray">
        
      </section>
      <section
        v-else
        class="gray">
        
      </section>
    </section>
    <section
      v-else
      class="green"
      @click="showOperations(audit)">
      <span class="audit-operation"></span>
    </section>
  </div>
</template>

<script>
/**
 * 
 */

const prefixCls = "msgList";
export default {
  name: "GroupPostCommentAuditStatus",
  props: {
    audit: { type: Object, default: () => {} }
  },
  data: () => ({
    prefixCls
  }),
  methods: {
    /**
     * 
     * @Author   Wayne
     * @DateTime 2018-02-08
     * @Email    qiaobin@zhiyicx.com
     * @return   {[type]}            [description]
     */
    accept() {
      const { currentItem = {} } = this;
      const { group_id: group = 0, member_id: member = 0 } = currentItem;
      this.$Modal.remove();
      this.$http
        .patch(
          `/plus-group/currency-groups/${group}/members/${member}/audit`,
          {
            status: 1
          },
          {
            validateStatus: s => s === 201
          }
        )
        .then(({ data }) => {
          this.audit.audit_at = 1;
          this.audit.status = 1;
          this.$Message.success(data);
        })
        .catch(() => {});
    },
    /**
     * 
     * @Author   Wayne
     * @DateTime 2018-02-08
     * @Email    qiaobin@zhiyicx.com
     * @return   {[type]}            [description]
     */
    reject() {
      const { currentItem = {} } = this;
      const { group_id: group = 0, member_id: member = 0 } = currentItem;
      this.$Modal.remove();
      this.$http
        .patch(
          `/plus-group/currency-groups/${group}/members/${member}/audit`,
          {
            status: 2
          },
          {
            validateStatus: s => s === 201
          }
        )
        .then(({ data }) => {
          this.audit.audit_at = 1;
          this.audit.status = 2;
          this.$Message.success(data);
        })
        .catch(() => {});
    },
    showOperations(audit) {
      if (audit.feed === null) {
        this.$Message.error(",");
        return false;
      }
      this.currentItem = audit;
      const vm = this;
      this.$Modal.info({
        title: false,
        render(h) {
          return h("div", {}, [
            h(
              "button",
              {
                on: {
                  click: vm.accept
                }
              },
              ""
            ),
            h(
              "button",
              {
                on: {
                  click: vm.reject
                }
              },
              ""
            )
          ]);
        }
      });
    }
  }
};
</script>
<style lang="less" src="../style.less">
</style>
